.app {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-align: center;
  align-items: center;
  min-height: 100vh;
  background-color: #F6F8FC;
}
.app__wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-positive: 1;
  flex-grow: 1;
  padding: 0 40px;
}
.app__wrapper h1 {
  text-align: center;
}
.app__wrapper h3 {
  margin-bottom: 10px;
}
.app__wrapper input {
  margin-top: 30px;
}
.app__wrapper button {
  margin-top: 30px;
}
.app__side-bar {
  position: fixed;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
  width: calc(100% - 40px);
  height: calc(100vh - 80px);
  top: 80px;
  left: 100%;
  padding: 0 20px;
  background-color: white;
  -webkit-transition: left 1s ease;
  -o-transition: left 1s ease;
  transition: left 1s ease;
}
.app__side-bar--active {
  left: 0;
}
.app__side-bar h3 {
  margin-top: 40px;
  margin-bottom: 10px;
}
.app__document {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  height: 50px;
  border-bottom: 1px solid #F6F8FC;
  cursor: pointer;
}
.app__document:last-child {
  border: 0px;
}
.app__document:hover *,
.app__document--active * {
  color: #3751FF;
}
.app__document p {
  font-size: 14px;
  font-weight: 500;
  margin-left: 10px;
}
@media (min-width: 960px) {
  .app__side-bar {
    position: relative;
    width: 260px;
    height: 100vh;
    top: 0;
    left: 0;
    -webkit-transition: right 0s ease;
    -o-transition: right 0s ease;
    transition: right 0s ease;
  }
}



