.login {
  position: fixed;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 100%;
}
.login__wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-align: center;
  align-items: center;
  width: 500px;
  height: 100%;
  z-index: 0;
}
.login__form {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
  width: 80%;
  max-width: 450px;
  padding-bottom: 85px;
}
.login__form input {
  margin-bottom: 20px;
}
.login__background {
  -ms-flex-positive: 1;
  flex-grow: 1;
  background-color: #F6F8FC;
}
.login__logo {
  font-size: 24px;
  font-weight: 600;
  color: #3751FF;
  margin-bottom: 20px;
}
.login__logo span {
  font-weight: 400;
  color: #3751FF;
}



